import { Navigate, Outlet, useLocation, useNavigate } from "react-router-dom"

export default function ShoppingCartPage() {
  const location = useLocation()
  const navigate = useNavigate()

  const currentStep =
    location.pathname === "/SLOMO-HK/cart/list" ? (
      <ul className="steps py-8">
        <li className="step step-primary text-primary text-xl mx-16 font-semibold"></li>
        <li className="step cursor-default"></li>
        <li className="step cursor-default"></li>
      </ul>
    ) : location.pathname === "/SLOMO-HK/cart/fillnpay" ? (
        <ul className="steps py-8">
          <li className="step step-primary mx-16 cursor-pointer font-semibold hover:text-primary " onClick={() => {navigate(-1)}}></li>
          <li className="step step-primary text-primary text-xl font-semibold cursor-default"></li>
          <li className="step cursor-default"></li>
        </ul>
    ) :location.pathname === "/SLOMO-HK/cart/ordered" ? (
      <ul className="steps py-8">
          <li className="step step-primary mx-16 font-semibold cursor-default"></li>
          <li className="step step-primary font-semibold  cursor-default"></li>
          <li className="step step-primary font-semibold text-primary text-xl  cursor-default"></li>
        </ul>
    ) : <Navigate to="/SLOMO-HK/error" />

  return (
    <div className="bg-base-200 min-h-[50rem]">
      <div className="wrapper">
        <div className="pb-10">
          <div className="flex justify-center">{currentStep}</div>
          <Outlet/>
        </div>
      </div>
    </div>
  )
}
